.welcome__slider {
  height: $height__welcome-slider;
  width: $width__welcome-slider;

  .slider__item {
    height: $height__welcome-slider;
    width: $width__welcome-slider;
  }

  .slick-prev,
  .slick-next {
    border: $border__welcome-slider-arrow;
    bottom: $bottom__welcome-slider-arrow;
    height: $height__welcome-slider-arrow;
    top: auto;
    transform: none;
    width: $width__welcome-slider-arrow;

    &::before {
      font-size: 22px;
      line-height: 0.3;
    }
  }

  .slick-prev {
    left: $left__welcome-slider-arrow-prev;

    &::before {
      content: "\27F5";
    }
  }

  .slick-next {
    left: $left__welcome-slider-arrow-next;

    &::before {
      content: "\27F6";
    }
  }
}