@import "../logo";

.header__body {
  align-items: stretch;
  display: flex;
  justify-content: space-between;
  height: $height__header;
}

.header__item {
  align-items: center;
  border-right: $border-width__header-item solid $border-color__header-items;
  display: flex;
  justify-content: center;
  padding: $padding__header-item;

  &:first-child {
    border-left: $border-width__header-item solid $border-color__header-items;
  }

  &:last-child {
    padding: 0;
  }

  .socials {

    li {
      border-right: $border-width__header-item solid $border-color__header-items;

      &:last-child {
        border-right: none;
      }
    }
  }
}

.header__phone {
  flex-grow: 1;

  a {
    color: $color__phone-text;
    font-size: $font-size__phone-1;
    font-weight: $font-weight__phone-1;
    line-height: $line-height__phone-1;
    position: relative;
    text-decoration: none;

    &::before {
      background-color: #000000;
      bottom: -2px;
      content: '';
      display: block;
      height: 1px;
      left: 0;
      opacity: 0;
      position: absolute;
      right: 0;
      transition: opacity 0.3s;
    }

    &:hover,
    &:focus {

      &::before {
        opacity: 1;
      }
    }
  }
}

.header__address {

  svg {
    height: $height__svg-marker;
    margin: $margin__svg-marker;
    width: $width__svg-marker;

    circle {
      fill: $fill__marker-circle;
      transition: fill 0.3s;
    }

    path {
      fill: $fill__marker-center;
    }
  }

  a {
    color: $color__address-text;
    font-size: $font-size__address;
    font-weight: $font-weight__address;
    line-height: $line-height__address;
    text-decoration: none;

    &:hover,
    &:focus {

      &+svg {
        circle {
          fill: #00868A;
        }
      }
    }
  }
}