
/*************************
  BASE FONT
*************************/

// Base font family.
$font-family__base: "Gilroy", sans-serif;

// Base line height.
$line-height__base: 1.5;

// Base font size
$font-size__base: 62.5%;  //10px

// Base font weight
$font-weight__base: 500;



/*************************
  ELEMENT FONT
*************************/

// Title
$font-weight__title: 700;
$line-height__title: $line-height__base;

$font-size__title-1: 48px;

$font-size__title-2: 24px;


// Text Blocks
$font-weight__text: $font-weight__base;
$line-height__text: $line-height__base;

$font-size__text-1: 14px;

$font-size__text-2: 18px;


// Button (<a><button>)
$font-weight__btn: $font-weight__base;
$letter-spacing__btn: 0.05em;

$font-size__btn-1: 14px;
$line-height__btn-1: 1.14;

$font-size__btn-2: 18px;
$line-height__btn-2: 1.17;


// Phone Number
$font-size__phone-1: 16px;
$line-height__phone-1: 1.19;
$font-weight__phone-1: $font-weight__base;

$font-size__phone-2: 21px;
$line-height__phone-2: 1.5;
$font-weight__phone-2: 700;


// Header
$font-size__address: 16px;
$line-height__address: 1.19;
$font-weight__address: $font-weight__base;


// Section Welcome
$font-size__welcome-menu: 18px;
$font-weight__welcome-menu: $font-weight__base;
$line-height__welcome-menu: $line-height__base;