.innovations {
  max-width: 526px;
  margin-bottom: 80px;
  width: 100%;
  
  .blockMarker {
    background-color: #4FBFA5;
    margin-bottom: 30px;
  }

  .see_more_arrow {
    margin-right: 20px;
  }
}

.innovations__body {
  align-items: flex-start;
  background-color: #00868A;
  display: flex;
  flex-direction: column;
  padding: 40px 35px 40px 45px;
}

.innovations__text {
  color: #ffffff;
  font-size: 30px;
  line-height: 1.5;
  margin-bottom: 25px;
  width: 100%;
}

.innovations__more {
  align-items: center;
  display: flex;

  a {
    color: #ffffff;
    display: inline-block;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.5;
    letter-spacing: 0.05em;
    position: relative;
    text-transform: uppercase;
    text-decoration: none;

    &::before {
      background-color: #ffffff;
      bottom: -2px;
      content: '';
      display: block;
      height: 1px;
      left: 0;
      opacity: 0;
      position: absolute;
      right: 0;
      transition: opacity 0.3s;
    }

    &:hover,
    &:focus {

      &::before {
        opacity: 1;
      }
    }
  }
}

.innovations__img {
  height: 230px;
}