@import "../fonts.scss";

.wrapper {
  color: $color__body-text;
  display: flex;
  flex-direction: column;
  font-family: $font-family__base;
  font-weight: $font-weight__base;
  min-height: 100vh;

  *,
  *::before,
  *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  img {
    object-fit: cover;
  }
}

.container {
  margin: 0 auto;
  max-width: $width__site;
  padding: $padding__container;
  width: 100%;
}

.myMain {
  flex-grow: 1;
}

.blockFlexRow {
  display: flex;
  justify-content: space-between;
}

.blockHide {
  display: none;
}

.blockTitle {
  font-weight: $font-weight__title;
  line-height: $line-height__title;
}

.blockTitle--1 {
  font-size: $font-size__title-1;
}

.blockTitle--2 {
  font-size: $font-size__title-2;
}

.blockText {
  font-weight: $font-weight__text;
  line-height: $line-height__text;
}

.blockText--1 {
  font-size: $font-size__text-1;
}

.blockText--2 {
  font-size: $font-size__text-2;
}

.blockBtn {
  align-items: center;
  display: flex;
  border-radius: $border-radius__btn;
  font-weight: $font-weight__btn;
  justify-content: center;
  text-align: center;
}

.blockBtn--1 {
  font-size: $font-size__btn-1;
  line-height: $line-height__btn-1;
}

.blockBtn--2 {
  font-size: $font-size__btn-2;
  line-height: $line-height__btn-2;
}