.statistics {
  padding-right: 50px;
  z-index: 1;
}

.statistics__item {
  align-items: center;
  display: flex;
  margin-bottom: 15px;

  &:last-child {
    margin-bottom: 0;
  }
}

.statistics__number {
  color: rgba(0,0,0,0.3);
  font-size: 48px;
  line-height: 1.5;
  margin-right: 20px;

  &.active {
    color: #00868A;
    font-size: 96px;
  }
}

.statistics__text {
  color: rgba(0,0,0,0.3);

  &.active {
    color: #000000;
    font-size: 28px;
  }
}