@import "statistics";

.beauty {
  display: flex;
  flex-direction: column;
  max-width: 636px;
  margin-bottom: 80px;
  padding-top: 35px;
  width: 100%;

  .blockMarker {
    background-color: #4FBFA5;
    margin-bottom: 30px;
  }
}

.beauty__body {
  align-items: center;
  display: flex;
  justify-content: space-between;
  position: relative;
}

.beauty__img {
  height: 400px;
  position: absolute;
  top: 0;
  right: 0;
  width: 306px;
}