@import "communication";

.footer {

}

.footer__body {

}

.footer__top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 80px;
}

.footer__menu {
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
  margin: -12px;
}

.footer__menuItem {
  margin: 12px;
  max-width: 306px;
  width: 100%;
}

.footer__menuItemTitle {
  font-size: 21px;
  font-weight: 700;
  margin-bottom: 20px;
}

.footer__menuItemList {
  list-style: none;

  li {
    margin-bottom: 15px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  a {
    color: #000000;
    font-size: 14px;
    line-height: 1.5;
    position: relative;
    text-decoration: none;

    &::before {
      background-color: #000000;
      bottom: -2px;
      content: '';
      display: block;
      height: 1px;
      left: 0;
      opacity: 0;
      position: absolute;
      transition: opacity 0.3s;
      right: 0;
    }

    &:hover,
    &:focus {

      &::before {
        opacity: 1;
      }
    }
  }
}

.footer__communication {
  flex-shrink: 0;
  max-width: 306px;
  width: 100%;
}

.footer__bottom {
  border-top: $border-width__header-item solid $border-color__header-items;
  display: flex;
  justify-content: space-between;
}

.footer__item {
  align-items: center;
  border-right: $border-width__header-item solid $border-color__header-items;
  display: flex;

  &:first-child {
    border-left: $border-width__header-item solid $border-color__header-items;
    border-right: none;
  }

  .socials {

    li {
      border-right: $border-width__header-item solid $border-color__header-items;

      &:last-child {
        border-right: none;
      }
    }
  }
}

.footer__copyright {
  font-size: 12px;
  font-weight: 400;
  padding: 20px 31px 20px 25px;
}

.footer__policy {
  font-size: 12px;
  font-weight: 400;
  padding: 0 70px 0 35px;

  a {
    color: #000000;
    position: relative;
    text-decoration: none;

    &::before {
      background-color: #000000;
      bottom: -2px;
      content: '';
      display: block;
      height: 1px;
      left: 0;
      position: absolute;
      transition: opacity 0.3s;
      right: 0;
    }

    &:hover,
    &:focus {

      &::before {
        opacity: 0;
      }
    }
  }
}

.footer__itemSpecBtn {
  padding: 0 30px;
}

