.socials {
  display: flex;
  height: 100%;

  li {
    align-items: center;
    display: flex;
    height: 100%;
    padding: $padding__socials-item;
  }

  a {
    display: block;
    text-decoration: none;

    &:hover,
    &:focus {

      svg {
        fill: #00868A;
      }
    }
  }

  svg {
    height: $height__svg-social;
    fill: $fill__social;
    transition: fill 0.3s;
    width: $width__svg-social;
  }
}