.offers {
  margin-bottom: 100px;
  position: relative;

  &::before {
    background-color: #00868A;
    content: '';
    display:block;
    height: 350px;
    left: 0;
    opacity: 0.1;
    position: absolute;
    right: 0;
    top: 0;
    z-index: -1;
  }
}

.offers__body {
  padding-top: 80px;
}

.offers__title {
  color: #00868A;
  margin-bottom: 60px;
  text-align: center;
}

.offers__items {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: -12px;
}

.offers__item {
  display: flex;
  flex-direction: column;
  height: 344px;
  margin: 12px;
  position: relative;
  transition: transform 0.3s;
  text-decoration: none;
  width: 306px;

  &:hover {
    transform: translateY(-5px);
  }

  .blockMarker {
    background-color: #4FBFA5;
    left: 30px;
    position: absolute;
    top: 30px;
  }

  img {
    height: 230px;
  }
}

.offers__itemFooter {
  align-items: center;
  background-color: #00868A;
  display: flex;
  flex-grow: 1;
  padding: 25px 30px;

  span {
    color: #ffffff;
    font-size: 21px;
    font-weight: 700;
    line-height: 1.5;
    margin-right: 15px;
  }
}