.welcome__description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: $max-width__welcome-description;

  h2 {
    color: $color-_title-text-1;
    margin: $margin__welcome-description-title;
  }

  .blockText {
    color: $color__text-2;
    margin: $margin__welcome-description-text;
    max-width: $max-width__welcome-description-text;
  }

  .blockBtn {
    align-self: flex-start;
    background-color: $bgc__btn-1;
    color: $color-_btn-text-1;
    padding: $padding__btn-2;
    text-transform: uppercase;
    transition: transform 0.3s;
    text-decoration: none;

    &:hover,
    &:focus {
      transform: scale(1.1);
    }
  }
}