.news {
  margin-bottom: 100px;

  .blockMarker {
    background-color: #00868A;
    margin: 40px 0 30px 30px;
  }
}

.news__body {
  display: flex;
  justify-content: space-between;
}

.news__sidebar {
  background-color: #4FBFA5;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  max-width: 526px;
  width: 100%;
}

.news__list {
  list-style: none;

  li {
    color: #ffffff;
    cursor: pointer;
    padding: 40px 35px;
    transition: background-color 0.3s;

    &.active,
    &:hover {
      background-color: #00868A;
    }
  }
}

.news__content {
  flex-grow: 1;
  padding: 60px 35px;
  position: relative;
  width: 100%;

  &::before {
    background-color: #00868A;
    bottom: 0;
    content: "";
    display: block;
    left: 0;
    opacity: 0.1;
    position: absolute;
    right: 0;
    top: 0;
  }
}

.news__contentItem {

}

.news__title {
  margin-bottom: 25px;
}

.news__text {

  li {
    display: flex;
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }

    &::before {
      background-color: #00868A;
      border-radius: 2px;
      content: '';
      display: block;
      flex-shrink: 0;
      height: 6px;
      margin: 8px 15px 0 0;
      position: relative;
      width: 6px;
    }
  }
}