.communication {

}

.communication__item {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;

  &:last-child {
    margin-bottom: 0;
  }
}

.communication__itemTitle {
  color: rgba(0,0,0,0.5);
  font-size: 11px;
  line-height: 1.5;
  letter-spacing: 0.05em;
  margin-bottom: 10px;
  text-transform: uppercase;
}

.communication__itemText {
  font-weight: 700;
  font-size: 21px;
  line-height: 1.5;

  a {
    color: #000000;
    transition: opacity 0.3s;
    position: relative;
    text-decoration: none;

    &::before {
      background-color: #000000;
      bottom: -2px;
      content: '';
      display: block;
      height: 1px;
      left: 0;
      opacity: 0;
      position: absolute;
      transition: opacity 0.3s;
      right: 0;
    }

    &:hover,
    &:focus {

      &::before {
        opacity: 1;
      }
    }
  }
}

.communication__itemBtn {
  align-self: flex-start;
  background-color: #00868A;
  color: #ffffff;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.05em;
  margin-top: 15px;
  padding: 14px 25px;
  text-transform: uppercase;
  transition: transform 0.3s;
  text-decoration: none;

  &:hover,
  &:focus {
    transform: scale(1.1);
  }

}