.specBtn {
  background-color: $bgc__btn-1;
  padding: $padding__btn-1;
  transition: transform 0.3s;
  text-decoration: none;

  &:hover,
  &:focus {
    transform: scale(1.1);
  }

  svg {
    height: $height__svg-eye;
    fill: $fill__eye;
    margin: $margin__svg-eye;
    width: $width__svg-eye;
  }

  span {
    color: $color__btn-text-1;
    text-transform: uppercase;
  }
}