@import "reg_btn";
@import "description";
@import "slider";
@import "categories";

.welcome {
  background-color: $bgc__welcome;
  margin-bottom: 80px;
}

.welcome__header {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: $padding__welcome-header;
}

.welcome__regBtn {
  margin: $margin__welcome-regBtn;
}

.welcome__menu {
  margin: $margin__welcome-menu;

  ul {
    align-items: center;
    display: flex;
    flex-grow: 1;
    justify-content: space-between;
    list-style: none;

    li {
      margin: $margin__welcome-menu-item;
    }

    a {
      color: $color__welcome-menu-text;
      display: block;
      font-size: $font-size__welcome-menu;
      font-weight: $font-weight__welcome-menu;
      line-height: $line-height__welcome-menu;
      transition: transform 0.3s;
      text-decoration: none;

      &:hover,
      &:focus {
        transform: translateY(-5px);
      }
    }
  }
}

.welcome__body {
  align-items: center;
  display: flex;
  justify-content: space-between;
}