.see_more_arrow {
  border: 1px solid #ffffff;
  color: #ffffff;
  flex-shrink: 0;
  height: 40px;
  font-size: 17px;
  line-height: 0.9;
  position: relative;
  width: 50px;

  &::before {
    content: '\27F6';
    display: block;
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}