.welcome__footer {

  ul {
    align-items: center;
    display: flex;
    justify-content: space-between;
    list-style: none;
  }

  li {
    &:first-child {
      flex-grow: 1;
    }

    &:nth-child(2){

      a {
        background-color: #4FBFA5;
      }
    }

    &:not(:first-child) {
      width: 324px;
    }
  }

  a {
    align-items: center;
    background-color: #00868A;
    color: #ffffff;
    display: flex;
    font-size: 21px;
    justify-content: center;
    line-height: 1.5;
    padding: 24px 0;
    transition: transform 0.3s;
    text-decoration: none;

    &:hover,
    &:focus {
      transform: translateY(-5px);
    }
  }
}